import { pages } from "projectConfig"
import React from "react"
import styled from "styled-components"
import { GatsbySeo } from "gatsby-plugin-next-seo"
import Row from "globalAtoms/Row"
import ElementList from "globalOrganisms/ElementList"
import Layout from "globalLayoutsStaticPage"

export default function ContactsPage(props) {
  const ContactsBlock = styled.div`
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 32px;
    margin-bottom: 64px;
    @media (max-width: 992px) {
      grid-template-columns: repeat(1, 1fr);
      grid-gap: 0;
    }
  `

  return (
    <>
      <GatsbySeo
        title={pages.contacts.title}
        description={pages.contacts.description}
        canonical={props.location.href}
        openGraph={{
          url: props.location.href,
          title: pages.contacts.title,
          description: pages.contacts.description,
        }}
      />
      <Layout className={"adaptive bg-primary-to-secondary"}>
        <section>
          <h1 className={"adaptive text mb-3"}>Контакты</h1>
          <article>
            <ContactsBlock>
              <ElementList
                title={"Техническая поддержка"}
                description={
                  "Консультации и решение любых проблем, связанных с заказами. График работы: будни с 9:00 до 18:00 "
                }
                listClassName={"adaptive border-outset rounded"}
              >
                <Row className={"element-medium"}>
                  <a
                    href={"https://t.me/veroniquel3bedeva"}
                    target={"blank"}
                    className={"left-part px-3"}
                  >
                    Телеграм
                  </a>
                </Row>
                <Row className={"element-medium"}>
                  <a
                    href={"mailto:acksiostm@gmail.com"}
                    target={"blank"}
                    className={"left-part px-3"}
                  >
                    E-mail
                  </a>
                </Row>
              </ElementList>
              <ElementList
                title={"Администрация"}
                description={
                  "Предложения сотрудничества. Решение проблем, не связанных с заказами. График работы: будни с 9:00 до 18:00 "
                }
                listClassName={"adaptive border-outset rounded"}
              >
                <Row className={"element-medium"}>
                  <a
                    href={"https://t.me/veroniquel3bedeva"}
                    target={"blank"}
                    className={"left-part px-3"}
                  >
                    Телеграм
                  </a>
                </Row>
                <Row className={"element-medium"}>
                  <a
                    href={"mailto:acksiostm@gmail.com"}
                    target={"blank"}
                    className={"left-part px-3"}
                  >
                    E-mail
                  </a>
                </Row>
              </ElementList>
            </ContactsBlock>
          </article>
        </section>
      </Layout>
    </>
  )
}
